@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

body{
    background-color:rgb(0, 0, 0);
}

#image{
   
    width: 100%;
    max-width: 500px;
    margin: 24px auto; 
    
}


h1 {
    text-align: center;
    font-family: 'Dancing script', cursive;
    font-size: 50px;
    color:white;
}

h2 {
    text-align: justify;
    font-family: 'Caveat', cursive;
    line-height: 1.74;
    margin-bottom: 5px;
    margin: auto;
    width: 60%;
    padding: 10px;
    text-align: justify;
    font-size: 40px;
}

.title {
    text-align: center;
    margin-bottom: 54px;
  }

p.date {
    text-align: justify;
    font-family: 'Dancing script', cursive;
    line-height: 1.74;
    margin-bottom: 5px;
    margin: auto;
    width: 60%;
    padding: 10px;
    text-align: justify;
    font-size: 25px;
}

/*align the text(for paragraphs) */

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 45%;
}




img {
    display: block;
    margin-left: auto;
    margin-right: auto;
}


p.solid {
      border-style: solid;

}

ul.no-bullets {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }


#lista {
    text-align: justify;
    font-family: 'Indie Flower', cursive;
    line-height: 1.74;
    margin-bottom: 5px;
    margin: auto;
    width: 60%;
   
    padding: 10px;

  
    text-align: justify;

    font-size: 25px;
}
  

  
li a {
    color: rgb(35, 23, 199);
    text-decoration:rgb(255, 255, 255);
}

#grad {
    background-image: linear-gradient(rgb(190, 215, 231), rgb(231, 189, 222));
  }